import React, { Component } from 'react';

class NavLink extends Component {

  constructor(props) {
    super(props);
    this.state = {
      active: this.props.active,
    };
  }

  render() {
    const className = this.props.active ? "nav-link active" : "nav-link";
    const id = this.props.linkId + "-link";
    return (
      <a className={className} id={id} data-toggle="pill" href={"#" + this.props.linkId} aria-controls={this.props.linkId} 
        onClick={() => this.props.onClick(this.props.linkId)}>
        {this.props.children}
      </a>
    );
  }
}

export default NavLink;



