import React, { Component } from 'react';
import ConsumedList from './components/ConsumedList';
import View from './View';
import Alert from './components/Alert';
import DateSearchBar from './components/DateSearchBar';
import * as DateUtil from './util/date';
import IntakeProcessor from './process/IntakeProcessor';

class ConsumptionView extends Component {

  constructor(props) {
    super(props);
    this.state = {
      searchDate: DateUtil.getCurrentIsoDate(),
    };
    this.searchByDate = this.searchByDate.bind(this);
    this.searchValueChange = this.searchValueChange.bind(this);
  }

  searchValueChange(event) {
    event.preventDefault();
    const searchDate = event.target.value;
    this.setState({ searchDate });
  }

  searchByDate(event) {
    event.preventDefault();
    this.props.doFilter(this.state.searchDate);
  }

  render() {
    const intakeSum = IntakeProcessor.calculateTotalIntake(this.props.consumedAliments);
    return (
      <View tabId={this.props.tabId} active={this.props.active}>
        <DateSearchBar title="Consuption" onSearchValueChange={this.searchValueChange} placeholder="Datetime ..."
          onSearch={this.searchByDate} defaultValue={this.state.searchDate} />
        <ConsumedList consumedAliments={this.props.consumedAliments} intakeSum={intakeSum} />
        <Alert title="Summary : " messages={[]} type="info">
          <ul>
            <li>You have digested <strong>{intakeSum}</strong> of <strong>0</strong> kcal.</li>
            <li>You can eat the equivalent of <strong>0</strong> kcal for the rest of this day</li>
          </ul>
        </Alert>
      </View>
    );
  }
}

export default ConsumptionView;



