
class ConsumptionModel {
    static  QTY_UNIT_GRAMME = "grammes";
    static  QTY_UNIT_UNIT = "unit";
    
    constructor(id, alimentId, datetime) {
        this.id = id;
        this.alimentId = alimentId;
        this.datetime = datetime;
        this.quantityUnit = this.QTY_UNIT_GRAMME;
        this.quantity = 0;
    }
}

export default ConsumptionModel;


