import React from 'react';
import NavLink from './NavLink';

class NavBar extends React.Component {

   constructor(props) {
      super(props);
      this.state = {
         navLinks: this.props.navLinks,
      }
   }

   toogleLink = linkId => {
      const navLinks = this.state.navLinks;
      const clickedLink = navLinks.filter(link => link.id === linkId)[0];
      const index = navLinks.indexOf(clickedLink);
      // disable all links
      navLinks.forEach(l => l.active = false);
      // active clicked link
      navLinks[index].active = true;
      this.setState({ navLinks });
      this.props.onToogleLink(this.state.navLinks);
   }

   render() {
      const buildLinks = this.state.navLinks.map((l, key) => <NavLink key={key} linkId={l.id} active={l.active} onClick={this.toogleLink}>{l.value}</NavLink>);
      return (
         <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
            <a className="navbar-brand" href="#">Fixed bottom</a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav"
               aria-expanded="false" aria-label="Toggle navigation">
               <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
               <div className="navbar-nav">
                  {buildLinks}
               </div>
            </div>
         </nav >
      );
   }
}
export default NavBar;



