import React from 'react';

function ViewPane(props) {
  return (
    <div className={props.active ? "tab-pane fade show active" : "tab-pane fade"}
      id={props.tabId} role="tabpanel" aria-labelledby={props.tabId + "-link"}>
      {props.children}
    </div>
  );
}
export default ViewPane;



