import SPFactory from '../StorageProcessorFactory';
import ConsumptionModel from '../models/ConsumptionModel';

class IntakeProcessor {

    static calculateIntake(consumption) {
        let intake = 0;
        const consumedAliment = SPFactory.getAlimentSP().findById(consumption.alimentId);
        if (ConsumptionModel.QTY_UNIT_GRAMME === consumption.quantityUnit) {
            intake = consumption.quantity * consumedAliment.energyPer100g / 100;
        } else if (ConsumptionModel.QTY_UNIT_UNIT === consumption.quantityUnit) {
            intake = consumption.quantity * consumedAliment.energyPerUnit;
        }
        return intake;
    }

    static calculateTotalIntake(consumptionList) {
        if(consumptionList.length < 1) return 0;
        const adder = (intake1, intake2) => intake1 + intake2;
        return consumptionList.map(c => this.calculateIntake(c)).reduce(adder);
    }
}

export default IntakeProcessor;


